<template>
  <div class="login" style="text-align: center">
    <div class="login-box">
      <div class="login-box-title">
        <img src="@/assets/Img/logo.png" alt="" />
        <div class="login-box-title-text">瑞通炭素</div>
      </div>
      <el-form>
        <el-form-item>
          <el-input v-model="account"><template><i class="el-icon-user loginIcon"></i></template></el-input>
        </el-form-item>
        <el-form-item>
          <el-input show-password v-model="password"><template><i
                class="el-icon-lock loginIcon"></i></template></el-input>
        </el-form-item>
      </el-form>
      <div class="login-box-footer">
        <el-button id='loginButton' @click.prevent="submitForm()" type="primary"
          style="width: 100%; padding: 12px 60px; font-size: 16px">登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { userLogin } from "@/api/login";
import { ref, reactive } from "vue";
import { useRouter } from "vue-router";
import { createModelToggleComposable, ElMessage, ElMessageBox } from "element-plus";
import { useCounterStore } from "../../store/index.js";
export default {
  setup() {
    let account = ref("");
    let password = ref("");
    const router = useRouter();
    function  enterButton() {
      //回车登陆
      document.body.onkeydown = function (event) {//当键盘摁下时触发
        if (event.key == "Enter") {//判断当摁下的键为回车时触发
          document.getElementById("loginButton").click();//获取id为loginButton的节点并点击
        }
      };
    }



    function submitForm() {
      const payload = {
        account: account.value,
        password: password.value,
      };
      userLogin(payload).then((res) => {
        console.log(res);
        if (res.data.code == 200) {
          router.replace("/main");
          ElMessage.success("欢迎," + res.data.data.name);
        } else if (res.data.message == "账号错误") {
          ElMessage.error("账号错误");
        } else {
          ElMessage.error("密码错误");
        }
        localStorage.setItem("loginToken", res.data.data.token);
        console.log(localStorage.getItem("loginToken"));
      });
    }
    return {
      account,
      password,
      submitForm,
    };
  },

};
</script>

<style lang="scss" scoped>
.login {
  width: 100vw;
  height: 100vh;
  background-image: url("../../assets/Img/backg.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  padding-top: 200px;
}

.login-box {
  width: 420px;
  // box-shadow: 2px 3px 7px #0003;
  backdrop-filter: blur(20px);
  margin: 0 auto;

  .login-box-title {
    display: flex;
    text-align: center;
    justify-content: center;
    margin: 30px auto;

    img {
      vertical-align: text-top;
      width: 42px;
      height: 42px;
    }

    &-text {
      font-size: 32px;
    }
  }

  &-footer {
    margin-bottom: 12px;
  }
}

.loginIcon {
  font-size: 16px;
}
</style>